export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			list: [],
			listCur: '',
			listCurID: '',
			boxTop: 0,
			barTop: 0,
			hidden: true
		}
	},
	computed: {
		scrollStyle() {
			let that = this
			let CustomBar = that.CustomBar
			// var style = `height:calc${(1350 - CustomBar)}px` calc(100vh-${CustomBar}px)
			var style = `position: absolute;top: ${(CustomBar + 50)}px;height: calc(100vh - ${CustomBar}px - 50px)`
			return style;
		},
		barStyle() {
			let that = this
			let CustomBar = that.CustomBar
			var style = `height: calc(100vh - ${CustomBar}px - 50px)`
			return style;
		}
	},
	created() {
		let that = this;
		uni.createSelectorQuery().select('.indexBar-box').boundingClientRect(function(res) {
			console.log(res)
			that.boxTop = res.top
		}).exec();
		uni.createSelectorQuery().select('.indexes').boundingClientRect(function(res) {
			that.barTop = res.top
		}).exec()
	},
	mounted() {
		let that = this
		that.initList()
	},
	methods: {
		initList() {
			let that = this
			let list = []
			for (let i = 0; i < 26; i++) {
				list[i] = String.fromCharCode(65 + i)
			}
			that.list = list
			that.listCur = list[0]
		},
		//获取文字信息
		getCur(e) {
			let that = this
			that.hidden = false
			that.listCur = that.list[e.target.id]
		},
		setCur(e) {
			let that = this
			that.hidden = true
		},
		tMove(e) {
			let y = e.touches[0].clientY,
				offsettop = this.data.boxTop,
				that = this;
			//判断选择区域,只有在选择区才会生效
			if (y > offsettop) {
				let num = parseInt((y - offsettop) / 20);
				that.listCur = that.list[num]
			};
		},
		tStart() {
			let that = this
			that.hidden = false
		},
		tEnd() {
			let that = this
			that.hidden = true
			that.listCurID = that.listCur
		}
	}
}
